import { getCountryByName } from '~/api/countries';
import { json, useLoaderData } from 'superjson-remix';
import { LoaderFunction, useParams } from 'remix';

// Language: typescript
// Path: app\routes\$name.tsx
export function useCountryByName() {
  const { name } = useParams();
  const loader: LoaderFunction<any> = async () => {
    const response = await fetch(`https://restcountries.com/v3.1/name/${name}`);

    const country = await response.json();

    return country[0];
  };

  return useLoaderData(loader);
}

// Language: typescript
// Path: app\routes\$cca3.tsx
export const CountryByCCA3 = () => {
    const country = useLoaderData();
    return <div>
        <h1>Country by cca3</h1>
        <p>{country.name}</p>
        <p>{country.cca3}</p>
    </div>
}